body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'Zelda';
  src: url("/portafolio-laboratoria/static/media/The Wild Breath of Zelda.a697f18e.otf") format("opentype");
}

html, body, #root, .App, div {
  height: 100%;
  width: 100%;
  font-family: 'Zelda';
}


.App {
  /* background-image: url('./img/Background2.png'); */
  background-image: url(/portafolio-laboratoria/static/media/pexels-photo-1006107.b2cd4f84.png);
  background-size: cover;
  background-position: center;
  box-shadow: inset 0 0 0 1000px rgba(247, 245, 245, 0.2);
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
}

.menu-fade-appear {
  opacity: 0;
  z-index: 1;
}

.menu-fade-appear.menu-fade-appear-active {
  opacity: 1;
  transition: opacity 600ms linear;
}

.menu-fade-exit {
  opacity: 1;
}

.menu-fade-enter {
  opacity: 0;
  z-index: 1;
}

.menu-fade-enter.menu-fade-enter-active {
  opacity: 1;
  transition: opacity 600ms linear;
}

.menu-fade-exit.menu-fade-exit-active {
  opacity: 0;
  transition: opacity 600ms linear;
}

.menu-fade-exit-done {
  opacity: 0;
}

