    height: 30%;
    /* width: 380px; */
    width: 100%;
    margin: auto;
    text-align: center;
    display: inline-block;
}


.name {
    color: #ffffff;
    text-shadow: -2px 2px black, -2px 0 black, 2px 0 black, 0 -2px black;
    font-size: 50px;
}

.title {
    color: #ffffff;
    text-shadow: -2px 2px black, -2px 0 black, 2px 0 black, 0 -2px black;
    font-size: 25px;
    margin-top: 10px;
}

.menu {
    color: #ffffff;
    text-shadow: -2px 2px black, -2px 0 black, 2px 0 black, 0 -2px black;
    font-size: 20px;
    margin-top: 40px;
}

.menu ul {
    list-style: none;
    padding: 0;
}

.menu li {
    vertical-align: middle;
    display: -webkit-flex;
    display: flex;
    height: 40px;
    cursor: pointer;
}

.menu li:focus {
    vertical-align: middle;
    display: -webkit-flex;
    display: flex;
    height: 40px;
    cursor: pointer;
    -webkit-animation: fade 1.4s infinite 100ms;
            animation: fade 1.4s infinite 100ms;
    outline: none;
}



.menu-icon {
    height: 40px;
    margin: auto;
    margin-right: 10px;
}

.menu-text {
    display: inline-block;
    margin: auto;
    margin-left: 10px;

}

.main-fade-appear {
    opacity: 0;
    z-index: 1;
}

.main-fade-appear.main-fade-appear-active {
    opacity: 1;
    transition: opacity 600ms linear;
}

.main-fade-enter {
    opacity: 0;
    z-index: 1;
}

.main-fade-enter.main-fade-enter-active {
    opacity: 1;
    transition: opacity 600ms linear;
}

.main-fade-exit {
    opacity: 1;
}

.main-fade-exit.main-fade-exit-active {
    opacity: 0;
    transition: opacity 600ms linear;
}

.main-fade-exit-done {
    opacity: 0;
}

@-webkit-keyframes fade {
    0% {opacity: 1;}
    30% {opacity: 0;}
    60% {opacity: 1;}
    100% {opacity: 1;}
} 

@keyframes fade {
    0% {opacity: 1;}
    30% {opacity: 0;}
    60% {opacity: 1;}
    100% {opacity: 1;}
}
button {
    padding: 0;
    border: none;
    font: inherit;
    color: inherit;
    background-color: transparent;
    /* show a hand cursor on hover; some argue that we
    should keep the default arrow cursor for buttons */
    cursor: pointer;
    margin: auto;
    display: -webkit-flex;
    display: flex;
    height: 100%;
    box-sizing: content-box; 
    /* padding: 10px;
    padding-right: 15px;  */
    
}

button:focus {
    outline: none;
}

.nav-bar {
    position: absolute;
    background-color: rgba(240, 235, 235, 0.6);
    z-index: 10;
    height: 10%;
    min-height: -webkit-fit-content;
    min-height: -moz-fit-content;
    min-height: fit-content;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

